/* Theme transition styles */
* {
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, box-shadow;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
}

/* Smooth theme transitions for specific elements */
html,
body,
[data-theme] {
  transition: background-color 500ms cubic-bezier(0.4, 0, 0.2, 1),
              color 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

/* Prevent transitions during page load */
.preload * {
  transition: none !important;
}

#root {
  width: 100%;
  height: 100vh;
}

/* Custom animations for the glowing button */
@keyframes glow {
  0%, 100% {
    box-shadow: 0 0 50px 10px rgba(45, 212, 191, 0.5);
  }
  50% {
    box-shadow: 0 0 80px 20px rgba(45, 212, 191, 0.8);
  }
}

@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

@keyframes pulse-glow {
  0%, 100% {
    opacity: 0.6;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.05);
  }
}

.animate-glow {
  animation: glow 2.5s ease-in-out infinite;
}

.animate-shimmer {
  animation: shimmer 3s linear infinite;
}

.animate-pulse-glow {
  animation: pulse-glow 5s ease-in-out infinite;
}

/* ============ Premium Teal Headline Effects ============ */
@keyframes teal-gradient-shift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

@keyframes teal-text-glow {
  0%, 100% {
    text-shadow:
      0 0 10px rgba(20, 184, 166, 0.35),
      0 0 22px rgba(20, 184, 166, 0.25),
      0 0 36px rgba(34, 211, 238, 0.15);
  }
  50% {
    text-shadow:
      0 0 14px rgba(20, 184, 166, 0.55),
      0 0 28px rgba(20, 184, 166, 0.35),
      0 0 52px rgba(34, 211, 238, 0.25);
  }
}

@keyframes teal-underline-sweep {
  0% { transform: translateX(-110%) scaleX(0.6); opacity: 0; }
  20% { opacity: 1; }
  60% { transform: translateX(10%) scaleX(1); opacity: 1; }
  100% { transform: translateX(110%) scaleX(0.6); opacity: 0; }
}

/* Animated teal gradient text */
.animated-teal-gradient {
  /* Dark-to-light teal gradient sweep */
  background: linear-gradient(90deg, #0f766e, #14b8a6, #5eead4, #14b8a6, #0f766e);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: teal-gradient-shift 6s ease-in-out infinite;
}

/* Soft shimmering teal glow on text */
.teal-glow-text {
  animation: teal-text-glow 3.2s ease-in-out infinite;
}

/* Underline sweep accent beneath highlighted words */
.teal-underline {
  position: relative;
}
.teal-underline::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -0.2em;
  height: 0.35em;
  border-radius: 9999px;
  background: radial-gradient(60% 100% at 50% 50%, rgba(20,184,166,0.45), rgba(34,211,238,0.25), rgba(20,184,166,0));
  filter: blur(4px);
  animation: teal-underline-sweep 2.4s ease-out infinite;
}

/* Optional subtle sparkle dots container for magical feel */
.sparkle-container {
  position: relative;
}
.sparkle-dot {
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 9999px;
  background: radial-gradient(circle, rgba(255,255,255,0.9) 0%, rgba(20,184,166,0.9) 40%, rgba(20,184,166,0) 70%);
  filter: blur(0.5px);
  opacity: 0.75;
  animation: sparkle-float 4.6s ease-in-out infinite;
}
@keyframes sparkle-float {
  0% { transform: translateY(0) translateX(0) scale(0.9); opacity: 0.0; }
  10% { opacity: 0.8; }
  50% { transform: translateY(-10px) translateX(6px) scale(1.05); }
  90% { opacity: 0.8; }
  100% { transform: translateY(0) translateX(0) scale(0.9); opacity: 0.0; }
}

/* ===== Industries Page Animations ===== */
@keyframes grid-pan {
  0% { background-position: 0 0, 0 0; }
  100% { background-position: 60px 60px, -60px -60px; }
}

.industries-hero-grid {
  position: absolute;
  inset: 0;
  background-image:
    linear-gradient(rgba(255,255,255,0.06) 1px, transparent 1px),
    linear-gradient(90deg, rgba(255,255,255,0.06) 1px, transparent 1px);
  background-size: 30px 30px, 30px 30px;
  animation: grid-pan 20s linear infinite;
}

@keyframes dash {
  0% { stroke-dasharray: 0 30; stroke-dashoffset: 30; opacity: 0.6; }
  50% { stroke-dasharray: 30 0; stroke-dashoffset: 15; opacity: 1; }
  100% { stroke-dasharray: 30 0; stroke-dashoffset: 0; opacity: 0.9; }
}
.stream-line {
  stroke-linecap: round;
  stroke-linejoin: round;
  animation: dash 4.5s ease-in-out infinite alternate;
}

@keyframes dotPulse {
  0%, 100% { transform: scale(1); filter: drop-shadow(0 0 2px rgba(45,212,191,0.4)); }
  50% { transform: scale(1.08); filter: drop-shadow(0 0 6px rgba(34,211,238,0.6)); }
}
.stream-dot { animation: dotPulse 3.2s ease-in-out infinite; }

/* ===== CTA Section (Physical AI) ===== */
@keyframes bgShift { 0% { filter: hue-rotate(0deg); } 100% { filter: hue-rotate(20deg); } }
@keyframes orbFloat { 0%, 100% { transform: translateY(0) translateX(0) scale(1); } 50% { transform: translateY(-10px) translateX(6px) scale(1.03); } }
@keyframes rippleExpand { to { transform: scale(1); opacity: 0; } }
@keyframes spin { to { transform: rotate(360deg); } }

.ai-pattern {
  background:
    radial-gradient(1000px 500px at 50% 120%, rgba(88, 28, 135, 0.15) 0%, rgba(10,10,10,0) 60%),
    repeating-linear-gradient(135deg, rgba(96,165,250,0.06) 0 2px, transparent 2px 10px),
    repeating-linear-gradient(45deg, rgba(167,139,250,0.06) 0 2px, transparent 2px 10px);
  mix-blend-mode: lighten;
  animation: bgShift 10s linear infinite alternate;
  will-change: transform;
}

.ai-orbs::before, .ai-orbs::after {
  content: '';
  position: absolute;
  border-radius: 9999px;
  filter: blur(40px);
  opacity: 0.6;
}
.ai-orbs::before {
  width: 420px; height: 420px; left: -120px; bottom: -100px;
  background: radial-gradient(circle, rgba(20,184,166,0.45), rgba(59,130,246,0.25), rgba(10,10,10,0));
  animation: orbFloat 6s ease-in-out infinite;
}
.ai-orbs::after {
  width: 360px; height: 360px; right: -100px; top: -80px;
  background: radial-gradient(circle, rgba(168,85,247,0.35), rgba(34,197,94,0.15), rgba(10,10,10,0));
  animation: orbFloat 5s ease-in-out infinite reverse;
}

/* Primary CTA button with energy pulse and ripple */
.cta-energy-button {
  display: inline-flex; align-items: center; justify-content: center;
  padding: 0.9rem 1.4rem;
  border-radius: 9999px;
  font-weight: 700; letter-spacing: 0.01em;
  color: #ffffff;
  background: linear-gradient(135deg, #2563eb 0%, #7c3aed 50%, #22d3ee 100%);
  box-shadow: 0 10px 30px rgba(124,58,237,0.35), 0 0 0 1px rgba(124,58,237,0.25) inset;
  transition: transform .18s ease, box-shadow .25s ease, filter .25s ease;
  position: relative; overflow: hidden; isolation: isolate;
}
.cta-energy-button::before {
  content: ''; position: absolute; inset: -1px; border-radius: inherit;
  background: conic-gradient(from 140deg, rgba(34,211,238,0.08), rgba(124,58,237,0.08), rgba(37,99,235,0.08));
  filter: blur(6px); z-index: -1;
}
.cta-energy-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 14px 40px rgba(124,58,237,0.45), 0 0 24px rgba(37,99,235,0.35), 0 0 0 1px rgba(34,211,238,0.4) inset;
  filter: saturate(1.15);
}
.cta-energy-button:active { transform: translateY(0px) scale(0.98); }
.cta-energy-button .ripple {
  position: absolute; border-radius: 9999px;
  background: radial-gradient(circle, rgba(255,255,255,0.85) 0%, rgba(124,58,237,0.45) 40%, rgba(34,211,238,0.35) 60%, rgba(0,0,0,0) 70%);
  transform: scale(0); animation: rippleExpand .6s ease-out forwards; pointer-events: none; opacity: .75;
}

/* Newsletter form styles */
.newsletter-form { text-align: center; }
.newsletter-input {
  background: rgba(255,255,255,0.04);
  border: 1px solid rgba(148,163,184,0.25);
  color: #e2e8f0;
  border-radius: 9999px;
  padding: 0.85rem 1.1rem;
  transition: box-shadow .2s ease, border-color .2s ease, transform .2s ease;
}
.newsletter-input::-moz-placeholder { color: #94a3b8; }
.newsletter-input::placeholder { color: #94a3b8; }
.newsletter-input:focus {
  outline: none; border-color: rgba(34,211,238,0.55);
  box-shadow: 0 0 0 3px rgba(34,211,238,0.18), 0 10px 30px rgba(124,58,237,0.15);
  transform: translateY(-1px);
}

.newsletter-submit {
  position: relative; border-radius: 9999px; padding: 0.85rem 1.1rem;
  font-weight: 700; color: #0a0a0a;
  background: linear-gradient(135deg, #5eead4 0%, #22d3ee 40%, #a78bfa 100%);
  overflow: hidden; border: 1px solid rgba(34,197,94,0.0);
}
.newsletter-submit .label { position: relative; z-index: 2; }
.newsletter-submit .spinner, .newsletter-submit .check {
  position: absolute; inset: 0; display: flex; align-items: center; justify-content: center;
  opacity: 0; pointer-events: none; transition: opacity .2s ease;
}
.newsletter-submit .spinner::before {
  content: ''; width: 18px; height: 18px; border: 2px solid rgba(10,10,10,0.6);
  border-top-color: rgba(10,10,10,1); border-radius: 9999px; animation: spin 0.8s linear infinite;
}
.newsletter-submit .check::before { content: "\2713"; font-size: 18px; color: #0a0a0a; }
.newsletter-submit.is-loading .label { opacity: 0; }
.newsletter-submit.is-loading .spinner { opacity: 1; }
.newsletter-submit.is-done { background: linear-gradient(135deg, #a7f3d0 0%, #86efac 50%, #5eead4 100%); }
.newsletter-submit.is-done .label { opacity: 0; }
.newsletter-submit.is-done .check { opacity: 1; }

.success {
  margin-top: .75rem; color: #a5f3fc; background: rgba(2,6,23,0.7);
  border: 1px solid rgba(34,211,238,0.25); backdrop-filter: blur(6px);
  border-radius: 12px; padding: .6rem .8rem; transform: translateY(-8px);
  opacity: 0; transition: transform .28s ease, opacity .28s ease;
}
.success.show { transform: translateY(0); opacity: 1; }

